<template>
    <div class="NAV-BAR ROOT">
         <!-- <span id="downclip"  :data-navbar-style="data.navstyle"> </span> -->
         <span id="downclip"  :data-navbar-style="cnav"> </span>
            <textb id="searchICON" :data="{text:'⚯'}" /> 
            <textb  id="subHEADING" :data="{text:data.subtitle}" />
            <textb  id="HEADING" :data="{text:data.title}" />
        
    </div>
</template>
<script>
import textb from './v.s.text.vue'
export default {
    name:'NAV-BAR',
    components:{
        textb
    },
    data()
    {
        return {
            navStyles:['right','left', 'diag','top'],
            cnav:'right'
        }
    },
    methods:
    {   
        start()
        {  var t=this; 
            setInterval(()=>
            {
                console.log((t.navStyles.indexOf(t.cnav)+1)%4);
                t.cnav=t.navStyles[(t.navStyles.indexOf(t.cnav)+1)%4];
                
            },
            2000
            )
        }
    },
    props:{
        data:Object,
        // navstyle:{
        //     type:String,
        //     default:'right'
        // }
    }
}
</script>
<style lang="less">
.NAV-BAR
{
    
    text-shadow: 0px 0px 10px black;
    // text-align: center;
    z-index: 1 !important;
    mix-blend-mode: multiply;
    #downclip
    {
            height:9em;
            width: 100%;
            position: absolute;
            top: 0px;
            left: 0px;
            background:black;
            

            display: grid;
            justify-content: center;
            align-content: center;
        
            padding-top:2em;
            text-align: center;
        &[data-navbar-style="right"]
        {
            transform: skewY(9deg) scaleY(4);
            transform-origin: 0 100%;
        }
        &[data-navbar-style="full"]
        {
                transform: scaleY(10) translateY(15vh);
                transform-origin: 0 100%;
        }
        &[data-navbar-style="top"]
        {
                transform: scaleY(1.5) ;
                transform-origin: 50% 50%;
        }
        &[data-navbar-style="left"]
        {
                transform: skewY(-9deg) scaleY(4);
                transform-origin: 100% 100%;
        }
        &[data-navbar-style="diag"]
        {
                transform: skewY(20deg) scaleY(4);
                transform-origin: 0 50%;
        }
    
}
#HEADING
    {
            margin-top: 10vmin;
            color: white;
            position: absolute;
            z-index: 4;
            font-size: 12vmin;
            right: 13vw;
    }
#subHEADING,#searchICON
    {
            margin-top: 0.5em;
            color: white;
            position: absolute;
            z-index: 4;
            font-size: 7vmin;
            left: 10vw;
    }
    #searchICON
    {
        cursor: pointer;
            left: 1vw;
            top: 0vw;
            margin: 0px;
            font-size: 7vmin;
    }
}
</style>



